<template>
  <div>
<nav-bar :name="title"></nav-bar>
        <v-container class="text-center">
        <router-link to="/addreferral">
          <v-btn color="#E47911" large height="40px" dark>
            <v-icon
              left
              dark
              > mdi-plus 
            </v-icon>  Add a new referral
          </v-btn>
        </router-link>
        </v-container>
          <v-card class="ma-auto pb-5" width="90%" outlined>
            <v-container class="text-center">
                <v-card>
            <v-layout>
            <v-col cols="11" class="text-right">
              <v-btn
                class="mx-auto"
                icon
                right
                @click="activeDelete"
              >
                <v-icon size="24px" :color="delete_btn_color">
                  mdi-delete
                </v-icon>
              </v-btn>
            </v-col>
            </v-layout>
              <v-card-title>
                <v-text-field
                  v-model="search"
                  append-icon="mdi-magnify"
                  label="Search"
                  single-line
                  hide-details
                ></v-text-field>
              </v-card-title>
              <v-data-table
                :headers="headers"
                :search="search"
                :items="referrals"
                :loading="loading"
                loading-text="Loading... Please wait"
                hide-default-footer
                :page.sync="page"
                :items-per-page="itemsPerPage"
                @page-count="pageCount = $event"
                >
                <template v-slot:item.unique_id="{ item }">
                  <v-menu
                    top
                    offset-x
                    v-if="select"
                  >
                    <template v-slot:activator="{ on, attrs }">
                    <v-btn icon
                      dark
                      v-bind="attrs"
                      v-on="on"
                    >
                        <v-icon right color="#E47911" size="26px" class="mb-2"> mdi-arrow-down-drop-circle </v-icon>
                    </v-btn>
                    </template>
                    <v-list>
                    <v-list-item link>
                      <v-list-item-title>
                        <router-link :to="{ name: 'UpdateReferral', params: { 'id': item.unique_id }}"> Update referral </router-link>
                      </v-list-item-title>
                    </v-list-item>
                    </v-list>
                  </v-menu>
                    <v-checkbox
                      :value="item.unique_id"
                      color="#E47911"
                      v-model="idReferral"
                      v-else
                      class="my-n3"
                    ></v-checkbox>
                </template>
              </v-data-table>
                </v-card>
                <br>
              <v-pagination
                v-model="page"
                :length="pageCount"
              ></v-pagination>

              <v-row v-if="!select">
                <v-col>
                <v-btn
                class="mx-4 my-2"
                color="#989898"
                dark
                @click="desactivarBtn"
                >
                CANCEL
                </v-btn>
                <v-btn
                class="mx-4 my-2"
                color="#E47911"
                dark
                @click="delete_referrals"
                >
                DELETE
                </v-btn>
                </v-col>
              </v-row>
        </v-container>
          </v-card>

<footer-nav></footer-nav>

  </div>
</template>

<script>
  import NavBar from '@/components/NavBar';
  import FooterNav from '@/components/Footer';
  import ReferralService from '@/services/ReferralService';
  import Swal from 'sweetalert2'

export default {
    components: {
      NavBar, FooterNav
    },

    data: () => ({
      title: 'REFERRALS',
      select: true,
      delete_btn_color: '#000',
      loading: true,
      page: 1,
      pageCount: 0,
      itemsPerPage: 5,
      search: '',
      referrals: [

      ],
      idReferral: [],
      headers: [
        {
          text: 'Referral',
          value: 'name'
        },
        {
          text: 'Date',
          value: 'registered'
        },
        { 
          text: 'Referred to',
          value: 'by'
        },
        { 
          text: 'Actions',
          value: 'unique_id',
          sortable: false,
          search: false
        },
      ],
    }),

    computed: {
      isStaff(){
        if (this.$store.state.auth.user.is_staff)
          return true;

        return false;
      },

      idStore(){
        if(!this.isStaff)
          return this.$store.state.auth.store_user.unique_id;
        
        return '';
      }
    },

    mounted(){
      this.refresh_list();
    },

    methods: {
      activeDelete(){
          this.select = !this.select;
          this.delete_btn_color = this.select ? '#000' : '#E47911'
      },

      desactivarBtn(){
          this.select = !this.select;
          this.delete_btn_color = '#000'
      },

      refresh_list(){
          this.loading = true
        if(this.isStaff){
          ReferralService
            .list_admin()
            .then(res => {
              this.referrals = res;
              this.loading = false
            })
            .catch(e => {
            let error = e.response.data;
            let msg = ''
            Object.entries(error).forEach(([ key, value ]) => {
              msg+= key + ': ' + value + ' \n ';
            });
              Swal.fire({
              icon: 'error',
              title: 'Error',
              text: msg
              });
          })
        }
        else {
          ReferralService
            .list_referrals_store(this.idStore)
            .then(res => {
              console.log(res)
              this.referrals = res;
              this.loading = false
          })
          .catch(e => {
            let error = e.response.data;
            let msg = ''
            Object.entries(error).forEach(([ key, value ]) => {
              msg+= key + ': ' + value + ' \n ';
            });
              Swal.fire({
              icon: 'error',
              title: 'Error',
              text: msg
              });
          })
        }
      },

      delete_referrals(){
        ReferralService
          .delete_referrals(this.idReferral)
          .then(res => {
            Swal.fire({
            icon: 'success',
            title: 'Success',
            text: 'Successful elimination'
            }).then(() => {
              this.refresh_list();
            })
          }).catch(e => {
                /* 
                *  SI HAY UN ERROR LO CAPTURA Y LO MUESTRA EN UNA MODAL
                */
               console.log(e.response.data)
    
                Swal.fire({
                icon: 'error',
                title: 'Error',
                text: e.response.data
                });
    
            })
      },

      


  },
}
</script>